const ethers = require('ethers');
const {Biconomy} = require("@biconomy/mexa");
class ClientContract{

    constructor(options){
        this.contract = {}
        this.metamaskProvider = {}
        this.BICONOMY_API_KEY = options.BICONOMY_API_KEY;
        this.abi = options.abi;
        this.contractAddress = options.contractAddress;
        this.network = options.network;
        this.instrument = options.instrument;
        this.oracletAbi = [{
            "inputs": [{
                "internalType": "address",
                "name": "_aggregator",
                "type": "address"
            }, {
                "internalType": "address",
                "name": "_accessController",
                "type": "address"
            }],
            "stateMutability": "nonpayable",
            "type": "constructor"
        }, {
            "inputs": [],
            "name": "latestAnswer",
            "outputs": [{
                "internalType": "int256",
                "name": "",
                "type": "int256"
            }],
            "stateMutability": "view",
            "type": "function"
        }, {
            "inputs": [{
                "internalType": "uint80",
                "name": "_roundId",
                "type": "uint80"
            }],
            "name": "getRoundData",
            "outputs": [{
                "internalType": "uint80",
                "name": "roundId",
                "type": "uint80"
            }, {
                "internalType": "int256",
                "name": "answer",
                "type": "int256"
            }, {
                "internalType": "uint256",
                "name": "startedAt",
                "type": "uint256"
            }, {
                "internalType": "uint256",
                "name": "updatedAt",
                "type": "uint256"
            }, {
                "internalType": "uint80",
                "name": "answeredInRound",
                "type": "uint80"
            }],
            "stateMutability": "view",
            "type": "function"
        }]
    }
    async getCurrentAccount(){
        const accounts = await this.metamaskProvider.listAccounts();
        return accounts.length > 0 ?  accounts[0] : ''
    }
    async getContractBalance(){
        return await this.metamaskProvider.getBalance(this.contract.address);
    }
    async getOwner(){
        return await this.contract.owner();
    }

    async init(){
        let url = this.network.chainData.rpcUrls[0]
        this.jsonRpcProvider = new ethers.providers.Web3Provider(window.ethereum)//.JsonRpcProvider(url);
        const biconomy = new Biconomy(window.ethereum,{apiKey: this.BICONOMY_API_KEY, debug: true});
        this.metamaskProvider = new ethers.providers.Web3Provider(biconomy);
        this.nonBiconomyContract = new ethers.Contract(this.contractAddress, this.abi, this.jsonRpcProvider);
        this.signerContract = new ethers.Contract(this.contractAddress, this.abi, this.metamaskProvider);
        //this.oracleContract = new ethers.Contract(this.network.instrument[this.instrument].oracle, this.oracletAbi, this.jsonRpcProvider);
    }

    async getBalance(address) {
       return await this.signerContract.balances(address)
    }

    async withdraw() {
        const withSigner = this.signerContract.connect(this.metamaskProvider.getSigner())
        return await withSigner.withdraw({gasLimit: 100000});
    }

    async deposit(amount) {
        const withSigner = this.signerContract.connect(this.metamaskProvider.getSigner())
        return await withSigner.deposit({value:amount});
    }

    async getPrice() {
        return await this.oracleContract.latestAnswer();
    }

    destroy() {
        try {
            if (this.signerContract) this.signerContract.removeAllListeners();
            if (this.signerContract) this.signerContract.removeAllListeners();
            if (this.oracleContract) this.oracleContract.removeAllListeners();
        } catch (e) {
            console.log(e);
        }
    }
}
module.exports = ClientContract